import { useEffect } from "react";

function Pay() {

  const headetClick = () => {
    let link = document.getElementsByClassName('header-bottom-content-link')
    for (let index = 0; index < link.length; index++) {
      link[index].classList.remove('header-bottom-content-link-activ')
      if (link[index].innerHTML === '/') {
        link[index].classList.add('header-bottom-content-link-activ')
      } 
    }
  }

  useEffect(()=>{
    headetClick()
  })
  return (
    <div className="pay page">
      <div className="conteiner">
        <div className="pay-inner">
          <div className="pay-content">
            <div className="pay-content-inner">
              <div className="pay-content-title">   </div>
              <div className="pay-content-desc">
                <div className="pay-content-desc-delivery">
                  <div className="pay-content-desc-titile">:</div>
                  <ul>
                    <li> -         1500 .</li>
                    <li>,  -        1500 , 1   .</li>
                    <li>      ,   500 .</li>
                  </ul>
                  <strong> :  42,   ,  FruitStories.</strong>
                </div>
                <div className="pay-content-desc-pay">
                  <div className="pay-content-desc-titile"> :</div>
                  <strong> ,  .</strong>
                  <div className="pay-content-desc-decs">   ,               .</div>
                </div>
                <div className="pay-content-desc-backUp">
                  <div className="pay-content-desc-titile"> :</div>
                  <div className="pay-content-desc-decs">      3     .<br/>  ,        5-30   (   ,     ).</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Pay;